export default {
  site(value) {
    if (!value) {
      return ''
    }
  },
  adc_listen(value) {
    if (!value) {
      return 'SubjectName'
    }

    if (value.length > 50) {
      return 'SubjectName50'
    }
  },
  listener(value) {
    if (!value) {
      return 'Listener'
    }

    if (value.length > 50) {
      return 'Listener50'
    }
  },
  listen_service(value) {
    if (!value) {
      return 'service'
    }

    if (value.length > 30) {
      return 'service30'
    }
  },
  listen_network(value) {
    if (!value) {
      return 'network'
    }

    if (value.length > 15 || value.length < 7) {
      return 'network715'
    }
  },
  listen_daemon(value) {
    if (!value) {
      return 'daemon'
    }

    if (value.length > 30) {
      return 'daemon30'
    }
  },
  adc_response(value) {
    if (!value) {
      return 'SubjectName'
    }

    if (value.length > 50) {
      return 'SubjectName50'
    }
  },
  responser(value) {
    if (!value) {
      return 'Listener'
    }

    if (value.length > 50) {
      return 'Listener50'
    }
  },
  resp_service(value) {
    if (!value) {
      return 'service'
    }

    if (value.length > 30) {
      return 'service30'
    }
  },
  resp_network(value) {
    if (!value) {
      return 'network'
    }

    if (value.length > 15 || value.length < 7) {
      return 'network715'
    }
  },
  resp_daemon(value) {
    if (!value) {
      return 'daemon'
    }

    if (value.length > 30) {
      return 'daemon30'
    }
  }
}
