
function checkCSVField(value,fieldText,required){
  if(required&&!value){
    return `${fieldText}`
  }
  if (value) {
    let arr = value.split(":")
    if(arr[0] && !(/^[_A-Z]{3,30}$/.test(arr[0]))){
      return `3-30_`
    }
    if(arr[1] && !(/^[_A-Za-z]{3,30}$/.test(arr[1]))){
      return `3-30_`
    }
    if((arr[0] && !arr[1])){
      return `${fieldText}`
    }
    if((!arr[0] && arr[1])){
      return `${fieldText}` 
    }
  }
}

export default {
  site(value, data) {
    if (!value) {
      return ''
    }
  },
  shop(value, data) {
    if (!value) {
      return ''
    }
  },
  public_path(value, data) {
    if (!value) {
      return ''
    }
    let sv = value.split(".");
    let errMsg = 'IP127.0.0.1';
    if(sv.length!=4){
      return errMsg
    }
    for(let s of sv){
      if(isNaN(s)){
        return errMsg
      }
      let intS = parseInt(s);
      if(s<0||s>255){
        return errMsg;
      }
    }
  },
  csv_path(value, data) {
    if (!value) {
      return 'CSV'
    }
  },
  nas_img_path(value, data) {
    if (!value) {
      return 'NAS'
    }
  },
  csv_img_path(value, data) {
    let ret = checkCSVField(value,"",true)
    if (ret) {
      return ret;
    }
  },
  csv_defect_size_path(value, data) {
    let ret = checkCSVField(value,"DEFECT_SIZE",true)
    if (ret) {
      return ret;
    }
  },
  csv_ratio(value, data) {
    let ret = checkCSVField(value,"RATIO")
    if (ret) {
      return ret;
    }
  },
  csv_archieve1(value, data) {
    let ret = checkCSVField(value,"Archieve1")
    if (ret) {
      return ret;
    }
  },
  
  editing_field(value) {
    console.log("editing_field:"+value)
    if(value){
      if(value=="/") {
        return ''
      }
      value = value.substring(1)

      let ps = value.split("/");
      const reg = /(\{)?([^\}\{\[]+)(\[0\-)?(\d*)(\])?(\})?/
      for(let str of ps){
          let m = reg.exec(str);
          if(m){
              console.log(m)
              if(m[1]){
                if(!m[2]){
                  return ''
                }
                if(m[3]){
                    if(!m[4]){
                      return '1-1000'
                    }
                    if(isNaN(m[4])){
                      return '1-1000'
                    }
                    if(parseInt(m[4])<1 || parseInt(m[4])>1000){
                      return '1-1000'
                    }
                }
              }
              else{
                if(!m[2]){
                  return ''
                }
              }
          }
          else{
            return ''
          }
      }

    } 
  }
}
